.grid__container {
  height: 100%;
  width: 100%;
  border-radius: 12%;
  margin: 0;
  position: relative;
  overflow: hidden;
}

.grid__container:hover, .grid__container:focus {
  cursor: pointer;
}

.grid__container:before {
  z-index: 2;
  content: "";
  width: 0;
  height: 0;
  opacity: 0;
  background: rgba(0, 0, 0, .2);
  border-radius: 100%;
  animation: bloom .75s ease-in-out forwards;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.grid__container:hover:before, .grid__container:focus:before {
  animation: circle .75s;
}

@keyframes bloom {
  0% {
    filter: grayscale(.8);
  }

  40% {
    filter: grayscale(.5);
  }

  100% {
    filter: grayscale(0);
  }
}

@keyframes circle {
  0% {
    opacity: .5;
    background: rgba(213, 156, 34, .2);
  }

  40% {
    opacity: 1;
    background: rgba(213, 34, 160, .2);
  }

  100% {
    width: 200%;
    height: 200%;
    opacity: 0;
  }
}

.grid__img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  border-radius: 12%;
  display: block;
}

.grid {
  grid-gap: 2px;
  grid-template-rows: repeat(15, 1fr);
  grid-template-columns: repeat(7, 1fr);
  margin-left: auto;
  margin-right: auto;
